*{
  padding:0;
  margin:0;
  box-sizing:border-box;
}
body
{ 
  margin:0;

}
.navbar
{
  background-color:white !important;
}
.content
{
  height:100vh;
}
.register-page
{
  display:flex;
  align-items:center;
  justify-content:center;
  height:90vh;
}
/* transection page*/
.filters
{
  display:flex;
  align-items:center;
  justify-content:space-between;
  padding:15px 20px;
  box-shadow:0 0 3px;
}
/*icons*/
.switch-icons
{
  border:1px solid rgb(0,0,0,0.677);
  border-radius:5px;
  padding:10px 15px;
}
.anticon svg{
  font-size: 20px;
  cursor:pointer;
}
.active-icon{
  color:black;
}
.inactive-icon{
  color:grey;
}